@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    font-family: 'Rubik', sans-serif ;
}
body{
    margin: 0;
}
.xnote{
    padding-left: 12px;
}
.xnotex{
    color: rgb(32, 153, 32);
}
.horizontalGreenLine{
    height: 2px;
    
    background: rgb(32, 153, 32);
}
.TaskListBody{
    padding: 12px;
    margin-top: 12px;
}
.TaskName,.TaskAddress{
    margin: 0;
    padding: 0;
}
.Task{
    border-radius: 8px;
    max-width: 400px;
    padding: 12px;
    margin: 16px 0px;
    background: rgb(246, 246, 246);
    display: flex;
}
.RemoveDiv{
    width: 100px;
    text-align: center;
    justify-content: center;
    padding:  6px 0;
}
.RemoveButton{
    display: inline-block;
    vertical-align: middle;
    line-height: 100%;
    border-radius: 100px;
    padding: 10px 16px;
}
.AddTaskButton{
    padding: 8px 40px;
    border: none;
    font-size: 24px;
    border-radius: 8px;
    color: white;
    background: rgb(65, 65, 65);
    text-decoration: none;
}
.AddTaskBody{
    padding: 12px;
    max-width: 400px;
}
.AddTaskBody input{
    width: 100%;
    padding: 0;
    font-size: 18px;
    line-height: 44px;
    margin-bottom: 6px;
    border-radius: 8px;
    border: none;
    background: rgba(32, 153, 32, 0.14);
}
.AddTaskBody button{
    width: 100%;
    padding: 0;
    color: white;
    font-size: 24px;
    line-height: 44px;
    border-radius: 8px;
    border: none;
    background: rgb(65, 65, 65);
}